body {
  font-family: 'Noto Sans TC';
}

.fs-33 {
  font-size: 33px;
}

.navbar-collapse {
  flex-grow: 0;
}

.text-hide {
  overflow: hidden;
  text-indent: 101%;
  white-space: nowrap;
}

.nav-scroller {
  height: 2.75rem;
  overflow: hidden;
  border-bottom: 1px solid #C0A4A5;
}

.nav-scroller .nav {
  flex-wrap: nowrap;
  padding-bottom: 1rem;
  justify-content: center;
  overflow-x: auto;
  white-space: nowrap;
}

.nav-link {
  color: #C0A4A5;
  position: relative;
  border: 1px solid #C0A4A5;
  border-radius: 15px;
}

.nav-link:hover {
  opacity: 0.7;
}

.nav-link:hover::before {
  width: 100%;
}

.logo {
  width: 96px;
  height: 31px;
  display: block;
  background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/yoga-week8/DOYOGA-logo.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
